.shape {
  -webkit-animation: spin 5s infinite linear;
          animation: spin 5s infinite linear;
  grid-row: 2;
  height: 17vh;
  left: 15%;
  margin: 6vh;
  position: absolute;
  top: -10%;
  -webkit-transform: scale(1) rotateX(-5deg) rotateY(-30deg);
          transform: scale(1) rotateX(-5deg) rotateY(-30deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: -webkit-transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
  width: 17vh;
}

.shape > div {
  background-color: rgba(255, 255, 255, 0.267);
  border: 1vh solid darkgreen;
  height: 17vh;
  opacity: 0.7;
  position: absolute;
  width: 17vh;
}

.shape span {
  display: block;
  font-size: 12vh;
  height: 100%;
  text-align: center;
  text-decoration: none;
  width: 100%;
}

.shape .ft {
  background-image: url('https://blog.betrybe.com/wp-content/uploads/2020/12/main_logo.png');
  background-position: center;
  background-repeat: no-repeat;
  -webkit-transform: translateZ(9vh);
          transform: translateZ(9vh);
}

.shape .rt {
  -webkit-transform: rotateY(90deg) translateZ(9vh);
          transform: rotateY(90deg) translateZ(9vh);
}

.shape .bk {
  background-image: url('https://blog.betrybe.com/wp-content/uploads/2020/12/main_logo.png');
  background-position: center;
  background-repeat: no-repeat;
  -webkit-transform: rotateY(180deg) translateZ(9vh);
          transform: rotateY(180deg) translateZ(9vh);
}

.shape .lt {
  -webkit-transform: rotateY(270deg) translateZ(9vh);
          transform: rotateY(270deg) translateZ(9vh);
}

.shape .tp {
  background-image: url('https://blog.betrybe.com/wp-content/uploads/2020/12/main_logo.png');
  background-position: center;
  background-repeat: no-repeat;
  -webkit-transform: rotateX(90deg) translateZ(9vh);
          transform: rotateX(90deg) translateZ(9vh);
}

.shape .bm {
  background-image: url('https://blog.betrybe.com/wp-content/uploads/2020/12/main_logo.png');
  background-position: center;
  background-repeat: no-repeat;
  -webkit-transform: rotateX(270deg) rotateY(0deg) translateZ(9vh);
          transform: rotateX(270deg) rotateY(0deg) translateZ(9vh);
}

.shape.show-ft {
  -webkit-transform: translateZ(9vh);
          transform: translateZ(9vh);
}

.shape.show-rt {
  -webkit-transform: rotateX(360deg) rotateY(-90deg);
          transform: rotateX(360deg) rotateY(-90deg);
}

.shape.show-bk {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.shape.show-lt {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
}

.shape.show-tp {
  -webkit-transform: rotateX(-90deg) rotateY(-360deg);
          transform: rotateX(-90deg) rotateY(-360deg);
}

.shape.show-bm {
  -webkit-transform: rotateX(90deg) rotateY(360deg);
          transform: rotateX(90deg) rotateY(360deg);
}

@-webkit-keyframes spin {

  0% {
    -webkit-transform: rotateX(-360deg) rotateY(-360deg);
            transform: rotateX(-360deg) rotateY(-360deg);
  }

  50% {
    -webkit-transform: rotateX(0deg) rotateY(0deg);
            transform: rotateX(0deg) rotateY(0deg);
  }

  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg);
            transform: rotateX(360deg) rotateY(360deg);
  }
}

@keyframes spin {

  0% {
    -webkit-transform: rotateX(-360deg) rotateY(-360deg);
            transform: rotateX(-360deg) rotateY(-360deg);
  }

  50% {
    -webkit-transform: rotateX(0deg) rotateY(0deg);
            transform: rotateX(0deg) rotateY(0deg);
  }

  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg);
            transform: rotateX(360deg) rotateY(360deg);
  }
}


