.trivia {
  background-image: url(/trivia/static/media/hehe.2bae00d7.jpg);
  background-size: cover;
}

.triviaCards {
  display: grid;
  grid-template-columns: 10% 60% 30%;
  grid-template-rows: 20% 60% 20%;
  height: 85vh;
  width: 100%;
}

.rankingBg {
  background-image: url(/trivia/static/media/hehefeed.8e408a09.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  grid-template-rows: 10vh 80vh 10vh;
  height: 100vh;
  margin: 0 auto;
  /* position: fixed; */
}

.rankingContainer {
  grid-row: 2;
}

.rankingBg a {
  align-self: center;
  grid-row: 1;
}

.rankingCards {
  align-self: center;
  -webkit-backdrop-filter: blur(4px) hue-rotate(45deg);
          backdrop-filter: blur(4px) hue-rotate(45deg);
  background-color: #80808033;
  box-shadow: black 2px 2px 10px 5px , black -2px -2px 10px 5px;
  display: inline-grid;
  height: 20vh;
  margin-bottom: 2vh;
  margin-left: 5vh;
  width: 40vh;
}

.rankingImg {
  height: 5vh;
}

.rankingName {
  align-self: center;
  color: #ffdead9e;
  font-size: 3vh;
  font-weight: 700;
  grid-column: 2;
  grid-row: 2;
  text-align: center;
  text-shadow: black 2px 1px 5px;
}

.rankingScore {
  align-self: baseline;
  color: #ffdead9e;
  font-size: 2vh;
  font-weight: 700;
  grid-column: 2;
  grid-row: 2;
  text-align: center;
  text-shadow: black 2px 1px 5px;
}

.rankingHeader {
  align-self: center;
  grid-row: 1;
  justify-self: center;
  margin-top: 0;
}

